<template>
  <v-dialog v-model="dialog" max-width="1200px">
    <v-card>
      <v-card-title>
        <span class="text-h6">{{ targetTitle }} </span>
      </v-card-title>
      <v-container fluid>
        <v-data-iterator
          :items="targetList"
          :items-per-page.sync="targetListPerPage"
          hide-default-footer
        >
          <template v-slot:default="props">
            <v-row>
              <v-col
                v-for="item in props.items"
                :key="item.name"
                cols="12"
                sm="6"
                md="4"
                lg="3"
              >
                <v-card>
                  <v-card-title class="subheading font-weight-bold">
                    {{ item.name }}
                  </v-card-title>

                  <v-divider></v-divider>

                  <v-list dense>
                    <v-list-item>
                      <v-list-item-content>Calories:</v-list-item-content>
                      <v-list-item-content class="align-end">
                        {{ item.calories }}
                      </v-list-item-content>
                    </v-list-item>

                    <v-list-item>
                      <v-list-item-content>Fat:</v-list-item-content>
                      <v-list-item-content class="align-end">
                        {{ item.fat }}
                      </v-list-item-content>
                    </v-list-item>

                    <v-list-item>
                      <v-list-item-content>Carbs:</v-list-item-content>
                      <v-list-item-content class="align-end">
                        {{ item.carbs }}
                      </v-list-item-content>
                    </v-list-item>

                    <v-list-item>
                      <v-list-item-content>Protein:</v-list-item-content>
                      <v-list-item-content class="align-end">
                        {{ item.protein }}
                      </v-list-item-content>
                    </v-list-item>

                    <v-list-item>
                      <v-list-item-content>Sodium:</v-list-item-content>
                      <v-list-item-content class="align-end">
                        {{ item.sodium }}
                      </v-list-item-content>
                    </v-list-item>

                    <v-list-item>
                      <v-list-item-content>Calcium:</v-list-item-content>
                      <v-list-item-content class="align-end">
                        {{ item.calcium }}
                      </v-list-item-content>
                    </v-list-item>

                    <v-list-item>
                      <v-list-item-content>Iron:</v-list-item-content>
                      <v-list-item-content class="align-end">
                        {{ item.iron }}
                      </v-list-item-content>
                    </v-list-item>
                  </v-list>
                </v-card>
              </v-col>
            </v-row>
          </template>
        </v-data-iterator>
      </v-container>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="close"> Close </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import { ref, watch, reactive, nextTick } from "vue";

export default {
  name: "DialogDetailItem",
  props: ["payload"],
  setup(props) {
    const targetListPerPage = ref(0);
    const targetList = ref([]);
    const targetTitle = ref("");

    const dialog = ref(false);
    const close = () => {
      dialog.value = false;
    };

    watch(
      () => props.payload,
      ({ title, list }) => {
        targetTitle.value = title;
        targetList.value = list;
        targetListPerPage.value = list.length;
        dialog.value = true;
      }
    );

    return {
      targetTitle,
      targetList,
      targetListPerPage,

      dialog,
      close,
    };
  },
};
</script>
