export const ruleRequired = (value) => {
  if (value === undefined || value === null || value === "") {
    return "This field is required";
  }
  return true;
};

export const ruleByte = (value) => {
  if (!value) return true;

  const byteLength = (function (s, b, i, c) {
    for (b = i = 0; (c = s.charCodeAt(i++)); b += c >> 11 ? 3 : c >> 7 ? 2 : 1);
    return b;
  })(value);

  if (byteLength > 50) {
    // return "You can input up to 50 bytes";
    return "This field is must be less than 50 bytes";
  }
  return true;
};
