<template>
  <v-dialog v-model="dialog" max-width="700px">
    <v-card>
      <v-card-title>
        <span class="text-h6">{{ targetTitle }} </span>
      </v-card-title>
      <v-form ref="formRef" @submit.prevent>
        <div class="regContainer">
          <div class="regBox" v-for="(item, idx) in targetFields" :key="idx">
            <v-text-field
              v-if="item.type === 'text'"
              v-model="updateItem[item.value]"
              :keyname="item.value"
              :rules="[ruleRequired, ruleByte]"
              :label="item.text"
              :placeholder="item.text"
              required
            ></v-text-field>
            <v-text-field
              v-if="item.type === 'number'"
              type="number"
              v-model="updateItem[item.value]"
              :keyname="item.value"
              :rules="[ruleRequired]"
              :label="item.text"
              :placeholder="item.text"
              required
              autocomplete="off"
            ></v-text-field>
            <v-text-field
              v-if="item.type === 'password'"
              v-model="updateItem[item.value]"
              :keyname="item.value"
              :rules="[ruleRequired]"
              :label="item.text"
              :placeholder="item.text"
              :type="passwordType"
              required
              autocomplete="off"
              @focus="handleType"
              @blur="handleType"
            ></v-text-field>
            <v-autocomplete
              v-if="item.type === 'select'"
              v-model="updateItem[selectKeys[item.api[1]].modelKey]"
              :keyname="item.value"
              @change="(value) => selectOnChange(value, item, updateItem)"
              :items="selectItems[item.api[1]]"
              :label="item.text"
              placeholder="Select..."
              required
              :item-text="selectKeys[item.api[1]].textKey"
              :item-value="selectKeys[item.api[1]].valueKey"
              return-object
            ></v-autocomplete>
            <v-checkbox
              v-if="item.type === 'checkbox'"
              v-model="updateItem[item.value]"
              :label="`${item.text}`"
              false-value="n"
              true-value="y"
            ></v-checkbox>
          </div>
        </div>
      </v-form>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
        <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import { ref, watch, reactive } from "vue";
import { getRequest, postRequest } from "@/services/request.js";
import { ruleRequired, ruleByte } from "@/plugins/validation.js";

export default {
  name: "DialogUpdateItem",
  props: ["payload"],
  setup(props) {
    let targetApi = null;
    let targetItem = null;
    let targetParentItem = null;

    const necessarySet = new Set();

    const formRef = ref();
    const updateItem = reactive({});
    const selectKeys = reactive({});
    const selectItems = reactive({});

    const targetFields = ref([]);
    const targetTitle = ref("");

    const dialog = ref(false);
    const close = () => {
      dialog.value = false;
    };

    const passwordType = ref("password");
    const handleType = (event) => {
      const { srcElement, type } = event;
      const { name, value } = srcElement;
      if (type === "blur" && !value) {
        passwordType.value = "text";
      } else {
        passwordType.value = "password";
      }
    };

    const selectOnChange = (value, item, updateItem) => {
      updateItem[item.value] = value.name;
      updateItem[item.modelKey] = value[item.valueKey];
    };

    const save = () => {
      const [fileName, apiName] = targetApi;
      const body = { ...targetParentItem, ...updateItem };

      let formHasError = false;

      formRef.value.$children.map((field) => {
        const keyname = field.$attrs.keyname;
        if (necessarySet.has(keyname) === true) {
          const value = body[keyname];

          if (value === undefined || value === null || value === "") {
            field.validate(true);
            formHasError = true;
          }
        }
      });
      if (formHasError === true) return;

      for (const key in props.payload.filter) {
        body[key] = props.payload.filter[key];
      }

      postRequest([fileName, `update_${apiName}`], body, (items) => {
        for (const i in items) {
          targetItem[i] = items[i];
        }
      });
      close();
    };

    watch(
      () => props.payload,
      ({ title, api, fields, item: payloadItem, parentItem }) => {
        targetItem = payloadItem;
        targetParentItem = parentItem || {};
        targetApi = api;

        const promises = [];
        const newFields = fields.filter((item) => {
          if (Object.prototype.hasOwnProperty.call(item, "type")) {
            console.log(item);

            if (item.necessary === true) {
              necessarySet.add(item.value);
            }

            updateItem[item.value] = payloadItem[item.value];

            if (item.type === "select") {
              const [fileName, apiName] = item.api;
              updateItem[item.modelKey] = payloadItem[item.modelKey];
              promises.push(
                new Promise((res) => {
                  getRequest(item.api, null, (items) => {
                    console.log(item.modelKey);
                    selectKeys[apiName] = {
                      textKey: item.textKey,
                      valueKey: item.valueKey,
                      modelKey: item.modelKey,
                    };
                    selectItems[apiName] = items;
                    res(true);
                  });
                })
              );
            }
            return true;
          }
          return false;
        });

        Promise.all(promises).then(() => {
          targetTitle.value = title;
          targetFields.value = newFields;
          dialog.value = true;
        });
      }
    );

    return {
      ruleRequired,
      ruleByte,

      selectKeys,
      selectOnChange,
      formRef,
      passwordType,
      handleType,

      targetTitle,
      targetFields,

      updateItem,
      selectItems,

      dialog,
      close,
      save,
    };
  },
};
</script>
