<template>
  <div class="component">
    <div class="box">
      <CreateDialog v-if="disbleNewItem !== true" :payload="createPayload" />
      <DeleteDialog v-if="disbleNewItem !== true" :payload="deletePayload" />
      <UpdateDialog v-if="disbleNewItem !== true" :payload="updatePayload" />
      <DetailDialog :payload="detailPayload" />

      <!--  -->
      <div class="col">
        <div class="box rowBox">
          <div class="hyTitle">{{ masterName }}</div>
          <v-select
            v-if="categoryOption !== undefined"
            @change="categoryChange"
            v-model="category"
            :items="categories"
            :item-text="(item) => item[categoryOption.textKey]"
            :item-value="(item) => item[categoryOption.valueKey]"
            menu-props="auto"
            hide-details
            prepend-icon="mdi-ferry"
            single-line
            style="width: 250px"
          >
          </v-select>
          <v-btn
            v-if="disbleNewItem !== true"
            color="primary"
            dark
            @click="
              createItem(masterName, masterApi, masterTableList, masterHeaders)
            "
          >
            New Item
          </v-btn>
          <div class="spacer"></div>
          <v-text-field
            v-model="masterTableSearch"
            append-icon="mdi-magnify"
            label=""
            single-line
            hide-details
          ></v-text-field>
          <v-btn
            v-if="formDownloadApi"
            color="black"
            dark
            @click="download(formDownloadApi)"
          >
            form Download
          </v-btn>
          <v-btn
            v-if="csvDownloadApi"
            color="black"
            dark
            @click="download(csvDownloadApi)"
          >
            csv Download
          </v-btn>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :single-select="true"
            :search="masterTableSearch"
            :headers="masterHeaders"
            :items="masterTableList"
            :items-per-page="-1"
            :item-key="masterTableKey"
          >
            <template v-slot:[`item.no`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon small class="mr-2" @click="detailItem(item)">
                mdi-text-box-search-outline
              </v-icon>
              <v-icon
                v-if="disbleNewItem !== true"
                small
                class="mr-2"
                @click="
                  updateItem(
                    masterName,
                    masterApi,
                    masterTableList,
                    masterHeaders,
                    item
                  )
                "
              >
                mdi-pencil
              </v-icon>
              <v-icon
                v-if="disbleNewItem !== true"
                small
                @click="deleteItem(masterApi, masterTableList, item)"
              >
                mdi-delete
              </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, watch, computed, nextTick } from "vue";
import { getRequest, fileDownloadRequest } from "@/services/request.js";
import DeleteDialog from "@/dialogs/DeleteItemDialog.vue";
import CreateDialog from "@/dialogs/CreateItemDialog.vue";
import UpdateDialog from "@/dialogs/UpdatItemDialog.vue";
import DetailDialog from "@/dialogs/DetailItemDialog.vue";
// import { useAuthStore } from "@/stories/mainStore.js";

export default {
  name: "DetailComponent",
  components: {
    CreateDialog,
    DeleteDialog,
    UpdateDialog,
    DetailDialog,
  },
  props: {
    obj: Object,
  },
  setup(props) {
    const {
      csvDownloadApi,
      formDownloadApi,

      categoryOption,
      disbleNewItem,
      detailProp,
      masterName,
      masterApi,
      masterHeaders,
    } = props.obj;

    const category = ref("");
    const categories = ref(null);

    const download = (api) => {
      fileDownloadRequest(
        api,
        { parent_key: category.value },
        (filename, blob) => {
          const url = URL.createObjectURL(blob);
          const a = document.createElement("a");
          a.href = url;
          a.download = filename;
          document.body.appendChild(a);
          a.click();
          window.URL.revokeObjectURL(url);
        }
      );
    };

    masterHeaders.splice(1, 0, {
      text: "No",
      value: "no",
      align: "center",
    });

    const masterTableList = ref([]);
    const masterTableSearch = ref("");
    const masterTableKey = ref(masterHeaders[0].value);

    const init = () => {
      const [fileName, apiName] = masterApi;
      if (category.value === "") {
        getRequest([fileName, apiName + "s"], null, (masterList) => {
          masterTableList.value = masterList || [];
        });
      } else {
        getRequest(
          [fileName, apiName + "s"],
          {
            filterKey: category.value,
          },
          (masterList) => {
            masterTableList.value = masterList || [];
          }
        );
      }
    };

    if (!categoryOption) {
      init();
    } else {
      const [fileName, apiName] = categoryOption.api;
      getRequest([fileName, apiName], null, (list) => {
        category.value = list[0][categoryOption.valueKey];
        categories.value = list;
        init();
      });
    }

    const categoryChange = (value) => {
      category.value = value;
      init();
    };

    //
    const createPayload = ref(null);
    const updatePayload = ref(null);
    const deletePayload = ref(null);
    const detailPayload = ref(null);

    const detailItem = (item) => {
      const detailList = [];
      masterHeaders.map((headerItem) => {
        if (headerItem.align === " d-none") {
          detailList.push({
            name: headerItem.value,
            value: item[headerItem.value],
          });
        }
      });
      detailPayload.value = {
        title: item[detailProp],
        list: detailList,
      };
    };

    let creater = null;

    if (disbleNewItem === true) {
      creater = {};
    } else {
      creater = {
        createItem(title, api, list, fields) {
          const parentItem = {};
          parentItem[masterTableKey.value] = null;
          parentItem.saveType = "new";

          createPayload.value = {
            title,
            api,
            list,
            fields,
            parentItem,
          };
        },
        updateItem(title, api, list, fields, item) {
          const parentItem = {};
          parentItem[masterTableKey.value] = item[masterTableKey.value];
          parentItem.saveType = "update";

          updatePayload.value = {
            title,
            api,
            list,
            fields,
            item,
            parentItem,
          };
        },
        deleteItem(api, list, item) {
          deletePayload.value = {
            list,
            item,
            api,
          };
        },
      };
    }

    return {
      ...creater,

      csvDownloadApi,
      formDownloadApi,
      download,

      masterTableKey,

      categoryChange,
      categoryOption,
      category,
      categories,

      //
      detailItem,
      disbleNewItem,
      masterName,
      masterApi,
      masterHeaders,
      masterTableList,
      masterTableSearch,

      detailPayload,
      createPayload,
      updatePayload,
      deletePayload,
    };
  },
};
</script>
