<template>
  <v-dialog v-model="dialog" max-width="500px">
    <v-card>
      <v-card-title class="text-h5">Delete</v-card-title>
      <p class="dialogContent">   ?</p>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
        <v-btn color="blue darken-1" text @click="confirm">OK</v-btn>
        <v-spacer></v-spacer>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import { ref, watch } from "vue";
import { deleteRequest } from "@/services/request.js";

export default {
  name: "DialogDeleteItem",
  props: ["payload"],
  setup(props) {
    let targetApi = null;
    let targetList = null;
    let targetItem = null;

    const dialog = ref(false);
    const close = () => {
      dialog.value = false;
    };
    const confirm = () => {
      const [fileName, apiName] = targetApi;
      deleteRequest([fileName, `delete_${apiName}`], targetItem, (res) => {
        const idx = targetList.indexOf(targetItem);
        targetList.splice(idx, 1);
      });
      close();
    };

    watch(
      () => props.payload,
      ({ api, list, item }) => {
        targetApi = api;
        targetList = list;
        targetItem = item;
        dialog.value = true;
      }
    );

    return {
      dialog,
      close,
      confirm,
    };
  },
};
</script>
